import React from 'react'
import {Link} from 'react-router-dom'
import './Pay.scss'
import $ from 'jquery'

// 
// import zhifubao from './images/zhifubao.png'
import Close from './images/Close.png'

// 
import {receiveGiftOrder , ActiveName , payZfb , payWx , checkReceive} from './../apis/api'

// 
import Loading from './../Components/Loading/Loading';

class Pay extends React.Component{

    componentDidMount(){
        var storage = window.localStorage
        var mid = storage.mid
        var _this = this
        this.checkRec = $.get(checkReceive , {mid:mid},function(res){
            if(res.data){
                _this.props.history.push('/')
            }
            else{
                console.log('meiyoulingquguo');
            }
        })
    }

    componentWillUnmount(){
        if(this.receiveGiftOrder){
            this.receiveGiftOrder.abort()
            console.log('1');
        }
        else if(this.payZfb){
            this.payZfb.abort()
            console.log('2');
        }
        else if(this.receiveGiftOrderItem){
            this.receiveGiftOrderItem.abort()
        }
        else if(this.payWx){
            this.payWx.abort()
        }
        else if(this.checkRec){
            this.checkRec.abort()
        }
    }

    zfbPay(e){
        e.preventDefault();
        this.refs.childRef.refs.Loading.style.display = "block"
        if(!window.localStorage){
            return false
        }
        else{
            var storage = window.localStorage
            var mid = storage.mid
            var This = this
            this.receiveGiftOrder = $.ajax({
                url: receiveGiftOrder,
                
                 data: {"mid" : mid, "name": ActiveName,"type" : 1},
                 type: "get",
                 dataType: 'json',
                 contentType: "application/x-www-form-urlencoded",
                 success: function (data) {
                    console.log(This.refs.childRef.refs.Loading);
                    console.log(data);

                    var id = data.data.id
                    var _this = This

                    this.payZfb = $.get(payZfb, {'id' : id},function(res){
                        window.location.href = res
                    })
                 }
             });
        }
    }
    wxPay(e){
        e.preventDefault();
        this.refs.childRef.refs.Loading.style.display = "block"
        if(!window.localStorage){
            return false
        }
        else{
            var storage = window.localStorage
            var mid = storage.mid
            this.receiveGiftOrderItem = $.ajax({
                url: receiveGiftOrder,
                
                 data: {"mid" : mid, "name": ActiveName,"type" : 2},
                 type: "get",
                 dataType: 'json',
                 contentType: "application/x-www-form-urlencoded",
                 success: function (data) {
                    console.log(data)

                    var id = data.data.id
                    this.payWx = $.get(payWx, {'id' : id},function(res){
                        window.location.href = res
                    })
                 }
             });
        }

    }

    Close(){
        console.log(this.refs.Pay);
        this.refs.Pay.style.display = 'none';
    }
    render(){
        return(
            <div className="PayWrap" ref="Pay">
                <div className="PayMain">
                    <div className="Pay">

                        <h1>8</h1>
                        <Link to="/" onClick={this.zfbPay.bind(this)}>
                            <div className="zhifubao"></div>
                        </Link>
                        <Link to="/PaySuc" onClick={this.wxPay.bind(this)}>
                            <div className="weChat"></div>
                        </Link>
                        
                        
                    </div>
                    <div className="Close">
                        <img src={Close} width="100%"/>
                        <span className="CloBtn" onClick={this.Close.bind(this)}></span>
                    </div>
                </div>

                <Loading ref="childRef"></Loading>
        
                
            </div>
        )
    }
}
export default Pay