import React from 'react'
import {Link} from 'react-router-dom'
import './Login.scss'
import $ from 'jquery'

// 
import LoginHeadImg from './images/LoginHeader.png'
import LoginBtnNo from './images/LoginBtnNo.png'
import LoginBtn from './images/LoginBtn.png'

// 
import Tips from './../Components/Tips/Tips'
import Loading from './../Components/Loading/Loading';

// 
import {verifyCode , mobileCode , checkReceive} from './../apis/api.js'

// 
import {SAVELOCAL} from './../Methods/Methods'


class Login extends React.Component{

    constructor(props){
        super(props)
        this.state = {
            phoneNum : '',
            codeNum :'',
            TipsInfo : '!!',
            codeText : '',
            color:'rgb(16, 156, 11)',
            timer : 60,
            liked : true,
            code:true,
            Login:true,
            isLogin : true
        }
    }

    componentDidMount(){
           
    }

    componentWillUnmount(){
        this.setState({
            liked : false
        })
        clearInterval(this.vix)
        // console.log('11');

        if(this.verify){
            this.verify.abort()
        }
        else if(this.checkRec){
            this.checkRec.abort()
        }
        else if(this.mobileCode){
            this.mobileCode.abort()
        }
    }

    // 
    enter(e){
        e.keyCode === 13 && this.handle()
    }
    handle(e){
        this.Login(e)
    }
    /***********/

    changePhonNum(e){
        let uPhon = e.target.value
        this.setState({
            phoneNum:uPhon
        })
        
        // console.log(uPhon);
        if(uPhon.length == 0 || uPhon.length < 11){
            this.setState({
                code   : true,
                codeText : '',
            })
        }
        else{
            this.setState({
                code : false,
            })
        }
    }
    checkPNum(e){

        var target = e.target
        target.value = target.value.replace(/\D/g,'')
    }
    changeCodeNum(e){
        let uCode = e.target.value
        console.log(uCode);
        this.setState({
            codeNum : uCode
        })
        if(uCode.length == 0 || uCode.length < 4 ){
            this.setState({
                Login : true
            })
        }
        else {
            // console.log('ss');
            this.setState({
                Login : false
            })
        }
    }

    // 
    Login(e){
        this.setState({
            Login : true
        })
        var _this = this
        this.refs.childRefTwo.refs.Loading.style.display = "block"
        let pattern = /^(13[0-9])|(14[5,7,9])|(15[^4])|(18[0-9])|(17[0,1,3,5,6,7,8])\d{8}$/
        if(this.state.phoneNum.length == 0 || this.state.phoneNum.length < 11 || !(pattern).test(this.state.phoneNum)){
            this.refs.childRef.refs.Tips.style.top = '0%' 
            setTimeout(function(){
                _this.refs.childRef.refs.Tips.style.top = '-40%'
            },2000)
            this.setState({
                TipsInfo : '!'
            })
        }
        else if(this.state.codeNum == 0 || this.state.codeNum.length < 4){
            this.refs.childRef.refs.Tips.style.top = '0%' 
            setTimeout(function(){
                _this.refs.childRef.refs.Tips.style.top = '-40%'
            },2000)
            this.setState({
                TipsInfo : '!'
            })

        }
        // e.preventDefault()
        console.log($("input").val())
        console.log($("#code").val())
        var phone = $("input").val()
        var code = $("#code").val()
        var storage = window.localStorage
        var ActPath = storage.ActPath
        var _this = this
        if(this.state.isLogin){
            this.verify = $.post(verifyCode, {mobile:phone ,code:code} , function(res){
                console.log(res);
                // var This =_this
                if(res.msg === 'SUCCESS'){

                    // console.log(This.refs.childRefTwo.refs.Loading)
                    _this.refs.childRefTwo.refs.Loading.style.display = "none"
                    
                    console.log('success')
                    SAVELOCAL('avatar' , res.data.avatar)
                    SAVELOCAL('count' , res.data.count)
                    SAVELOCAL('is_receive' , res.data.is_receive)
                    SAVELOCAL('keys' , res.data.keys)
                    SAVELOCAL('mid' , res.data.mid)
                    SAVELOCAL('nickname' , res.data.nickname)
                    SAVELOCAL('token' , res.data.token)
                    SAVELOCAL('be_mid' , res.data.mid)
                    // _this.setState({
                    //     isLogin : false
                    // })

                    if(ActPath){
                        _this.props.history.push(ActPath)
                    }
                    else{
                        var This = _this
                        var mid = storage.mid
                        this.checkRec = $.get(checkReceive , {mid : mid } ,function(res){
                            if(res.data){
                                console.log('')
                                This.props.history.push('/')
                            }
                            else{
                                console.log('')
                                This.props.history.push('/UserInfo')
                            }
                        })
                    }


                    
                }
                else{
                    _this.refs.childRef.refs.Tips.style.top = '0'
                    setTimeout(function(){
                        _this.refs.childRef.refs.Tips.style.top = '-40%'
                    },2000)

                    _this.setState({
                        TipsInfo : '!',
                        color: 'red',
                        Login : false
                    })
                    _this.refs.childRefTwo.refs.Loading.style.display = "none"
                }
                // if()
            
             })
        }
        // e.preventDefault()
        // console.log(this.refs.childRef.refs.Tips);
    }
    UngetCode(e){
        return false
    }
    UnLogin(e){
        e.preventDefault()
        return false
    }
    getCode(e){
        let _this = this
        // let pattern = /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/
        let pattern = /^(13[0-9])|(14[5,7,9])|(15[^4])|(18[0-9])|(17[0,1,3,5,6,7,8])\d{8}$/
        if(this.state.phoneNum.length == 0 || this.state.phoneNum.length < 11 || !(pattern).test(this.state.phoneNum)){
            this.refs.childRef.refs.Tips.style.top = '0%' 
            setTimeout(function(){
                _this.refs.childRef.refs.Tips.style.top = '-40%'
            },2000)
            this.setState({
                color:'red',
                TipsInfo : '!'
            })
            e.preventDefault()

        }
        // else if(!this.state.liked){
            
        //     return false
        // }
        else if(this.state.liked){
            let timer = this.state.timer
            this.setState({
                liked : false,
                code : true
            })
            clearInterval(this.vix)
            this.vix = setInterval(()=>{
                // !
                this.setState({timer:(this.state.timer-1)},()=>{
                    // console.log(timer);
                    if(this.state.timer < 0){
                        clearInterval(this.vix)
                        this.setState({
                            codeText : '',
                            liked:true,
                            timer:60,
                            code : false
                        })
                    }
                })
            },1000)
            // console.log($("input").val());
            // $.post(verifyCode)

            this.refs.childRef.refs.Tips.style.top = '0%' 
            setTimeout(function(){
                _this.refs.childRef.refs.Tips.style.top = '-40%'
            },2000)
            this.setState({
                color:"rgb(16, 156, 11)",
                TipsInfo : '!'
            })

            console.log($("input").val());
            let phoneNum = $("input").val()

            this.moblieCode = $.post(mobileCode , {mobile : phoneNum } ,function(res){
                console.log(res);
            })
           
        }
        
    }

    

    render(){
        return (
            <div className="LoginWrap">

                <Loading ref="childRefTwo"></Loading>
                <Tips 
                    ref="childRef"
                    info={this.state.TipsInfo}
                    color ={this.state.color}
                ></Tips>

                {/*  */}
                <header className="LoginHeaderWrap">
                    <div className="LoginHeader">
                        <img src={LoginHeadImg} width="100%"/>
                        <Link to="/">
                            <span className="LoginReturn"></span>
                        </Link>
                        
                    </div>
                </header>

                {/* */}
                <main className="LoginMainWrap">
                    <div className="LoginMain">
                        <div className="LoginForm" >
                            <form action="#">

                                <div className="phone">

                                    <input 
                                        type="text" 
                                        name="phone" 
                                        placeholder="" 
                                        autoComplete="off"
                                        maxLength="11"
                                        defaultValue = {this.state.phoneNum}
                                        onChange = {this.changePhonNum.bind(this)}
                                        onInput = {this.checkPNum.bind(this)}
                                    />

                                    <span 
                                        className={this.state.code ? 'Ucode' : 'code'}
                                        onTouchEnd={this.state.code ? this.UngetCode.bind(this) : this.getCode.bind(this)}
                                        
                                    >
                                        {this.state.liked ? this.state.codeText : this.state.timer}
                                    </span>
                                </div>

                                <div className="code">
                                    <input 
                                        type="text"
                                        id="code" 
                                        name="code" 
                                        placeholder="" 
                                        autoComplete="off"
                                        maxLength="4" 
                                        style={{marginTop : `${20}px`}}
                                        onChange = {this.changeCodeNum.bind(this)}
                                        onInput = {this.checkPNum.bind(this)}
                                        onKeyDown = {this.enter.bind(this)}
                                        
                                    />
                                    
                                </div>
                                
                            </form>
                        </div>
                        <div className="LoginBtn">
                            
                                <span className="LoginB" onTouchEnd={this.state.Login ? this.UnLogin.bind(this) : this.Login.bind(this)}></span>
                            
                            <img src={this.state.Login  ? LoginBtnNo : LoginBtn} width="100%"/>
                        </div>
                    </div>
                </main>
            </div>
        )
    }
}
export default Login