import React from 'react'
import {Link} from 'react-router-dom'
import './HomePage.scss'
import $ from 'jquery'

// 
import {checkReceive} from './../apis/api'

// 
import gotten from './images/gotten.png'
import close from './images/close.png'


// 
import Loading from '../Components/Loading/Loading';

class HomePage extends React.Component{
    constructor(props){
        super(props)
        this.state={

        }
    }
    componentWillUnmount(){
        if(this.checkRec){
            this.checkRec.abort()
            console.log('1')
        }
        else if(this.checkRecItem){
            this.checkRecItem.abort()
            console.log('2')
        }
    }
    componentDidMount(){
        var storage = window.localStorage
        var mid = storage.mid
        var _this = this
        this.checkRec = $.get(checkReceive , {mid : mid} , function(res){
            if(res.data){
                console.log('')
                _this.refs.gotten.style.display = "block"
                _this.refs.gottenWrap.style.display = "block"
            }
        })
    }

    closeGotten(){
        this.refs.gotten.style.display = "none"
        this.refs.gottenWrap.style.display = "none"
    }

    clear(){
        var storage = window.localStorage
        storage.clear()
    }

    getToken(e){
            var storage = window.localStorage
            var token = storage.token
            var mid = storage.mid
            var _this = this
            this.refs.childRef.refs.Loading.style.display = "block"
            // console.log(token);
            this.checkRecItem = $.get(checkReceive , {mid : mid } ,function(res){
                console.log(res)
                if(res.data){
                    console.log('')
                    _this.refs.childRef.refs.Loading.style.display = "none"
                    _this.refs.gotten.style.display = "block"
                    _this.refs.gottenWrap.style.display = "block"
                }
                else{

                    _this.refs.childRef.refs.Loading.style.display = "none"
                    
                    if(token){
                        _this.props.history.push('/UserInfo')
                    }
                    else{
                        _this.props.history.push('/Login')
                    }
                }
            })
            
        }

    render(){
        return(
            <div className="HPWrap">
                <Loading ref="childRef"></Loading>
                <h1>-item9</h1>
                <button  onTouchEnd={this.getToken.bind(this)}></button>
                <button onClick={this.clear.bind(this)}></button>

                {/**  */}
                <div className="gottenWrap" ref="gottenWrap"></div>
                
                <div className="gotten" ref="gotten">
                    <div className="gotten_wrap"> 
                        <span className="gotten_btn" onClick = {this.closeGotten.bind(this)}></span>
                        <img src={gotten} width="100%"/>
                    </div>
                    
                    <div className="closeGotten">
                        <span className="closeBtn" onClick = {this.closeGotten.bind(this)}></span>
                        <img src={close} width="100%"/>
                    </div>
                   
                </div>
            </div>
        )
    }
}
export default HomePage